.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.mapContainer {
  background-color: #282c34;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mapInner {
  height: calc(100vh - 50px);
  width: 100vw;
  box-shadow: 3px 0px 5px #3d4148, -3px 0px 5px #3d4148;
  max-width: 1024px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

canvas {
  position: unset !important;
}
